var division = function (accumulator) {
    let key = new Date().valueOf() + Math.floor(Math.random() * 100);
    let newAccumulator = accumulator.concat(` x${key}/y${key}`);

    return {
        key,
        x: '',
        y: '',
        accumulator: newAccumulator,

        setValues(x, y) {
            this.x = x;
            this.y = y;
        },

        getValue (accumulator) {
            accumulator = accumulator || this.accumulator;

            return accumulator
                .replace(`x${this.key}`, this.x)
                .replace(`y${this.key}`, this.y);
        },
    };
};

class MapEvents {
    constructor() {
        this.map = new Map();
    }

    set(key, callback) {
        this.map.set(key, callback);
    }

    get(key) {
        return this.map.get(key);
    }

    setMap() {
        this.setNumbersButtons();
        this.set("=", (accumulator) => (accumulator.concat(" =")));
        this.set(",", (accumulator) => (accumulator.concat(".")));
        this.set("+", (accumulator) => (accumulator.concat(" +")));
        this.set("-", (accumulator) => (accumulator.concat(" -")));
        this.set("*", (accumulator) => (accumulator.concat(" *")));
        this.set("/", division);
    }

    setNumbersButtons() {
        for (let index = 0; index <= 9; index++) {
            this.set(
                index.toString(),
                (accumulator) => (accumulator.concat(` ${index}`))
            );
        }
    }
}

export default new MapEvents();
